<template>
  <div class="pageContent">
    <el-card class="operate-container mt10" shadow="never">
      <div class="pageHead">
        <div class="flex-row-between">
          <div>提现申请详情</div>
          <div class="warningFont" v-if="info.withdrawType == 1">审核中</div>
          <div class="warningFont" v-if="info.withdrawType == 2">处理中</div>
          <div class="succeseFont" v-if="info.withdrawType == 3">提现成功</div>
          <div class="dangerFont" v-if="info.withdrawType == 4">提现失败</div>
          <div class="dangerFont" v-if="info.withdrawType == 5">已驳回</div>
        </div>
      </div>
      <div class="pageForm">
        <el-table :data="[info]" style="width: 100%">
          <el-table-column prop="withdrawNo" label="提现单号" ></el-table-column>
          <el-table-column prop="bankAccountName" label="持卡人" ></el-table-column>
          <el-table-column prop="openName" label="银行名称" ></el-table-column>
          <el-table-column prop="bankAccount" label="银行卡号" ></el-table-column>
          <el-table-column prop="openBranch" label="开户支行" ></el-table-column>
          <el-table-column label="提现金额">
            <template slot-scope="scope">￥{{scope.row.withdrawAmount}}</template>
          </el-table-column>
          <el-table-column label="手续费" >
            <template slot-scope="scope">￥{{scope.row.withdrawCommission}}</template>
          </el-table-column>
          <el-table-column prop="createTime" label="提现时间" ></el-table-column>
        </el-table>
      </div>
      <div class="reason">
        <el-form ref="form" :model="info" label-width="120px">
          <el-form-item label="流水号：">
            <div>{{info.paymentNo}}</div>
          </el-form-item>
          <!-- 已驳回 -->
          <el-form-item v-if="info.withdrawType == 5" label="驳回原因：">
            <div>{{info.auditReason}}</div>
          </el-form-item>
          <el-form-item v-if="info.withdrawType == 5" label="驳回时间：">
            <div>{{info.updateTime}}</div>
          </el-form-item>
          <!-- 成功 -->
          <el-form-item v-if="info.withdrawType == 3" label="提现到账时间：">
            <div>{{info.payTime}}</div>
          </el-form-item>
          <!-- <el-form-item v-if="info.withdrawType == 3" label="流水号：">
            <div>{{info.paymentNo}}</div>
          </el-form-item> -->
          <!-- 失败 -->
          <el-form-item v-if="info.withdrawType == 4" label="失败原因：">
            <div>{{info.reason}}</div>
          </el-form-item>
          <el-form-item v-if="info.withdrawType == 4" label="银行处理时间：">
            <div>{{info.payTime}}</div>
          </el-form-item>
          <div class="dangerFont" v-if="info.withdrawType == 4">温馨提示：提现失败，请联系筷赚相关财务人员。</div>
        </el-form>
      </div>
      <div class="reason">
        <el-button type="primary" @click="$router.push({path:'/assetsManage/account/record'})">返 回</el-button>
      </div>
    </el-card>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import { getShopWithdrawInfo } from '@/api/assetsManage/account'
export default {
  data(){
    return{
      info:{

      }
    }
  },
  created(){
    this.getData()
  },
  methods:{
    getData(){
      console.log('请求了')
      getShopWithdrawInfo(this.$route.query.id).then(res=>{
        console.log('提现详情',res)
        if(res.code == 200){
          this.info = res.data
        }
      })
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
}
</script>
<style lang="less" scoped>
.reason{
  margin-top: 20px;
}
</style>